import React, { Component } from "react";

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.imagesArray = [
      "https://steamuserimages-a.akamaihd.net/ugc/775120468189138006/C07D04B476175BD5FA5520375DB4BBB319A53240/",
      "https://steamuserimages-a.akamaihd.net/ugc/775120317425281124/178716279D148B9DB814B91A84AB860F9A8D40BD/",
      "https://steamuserimages-a.akamaihd.net/ugc/769490968655513520/2D05064321EF4EBB967A048450D53E4E65E25450/",
      "https://steamuserimages-a.akamaihd.net/ugc/771742963100018976/42FA741E6B8EA05E1BDF97EB6E0028625418FD77/",
      "https://steamuserimages-a.akamaihd.net/ugc/771741074309483065/B0C3745A0A3A1506B72161C711964DC2F240CA8D/",
      "https://steamuserimages-a.akamaihd.net/ugc/778478584860108665/2E206DDC5D6BDEBD9AE604F416B7EFDEF298B657/"
    ];
  }
  render() {
    return (
      <div>
        {this.imagesArray.map(url => (
          <img src={url}></img>
        ))}
        <button className="button is-danger comingFromDown">Log Out</button>
      </div>
    );
  }
}
